import React, { useState, useCallback, useMemo } from 'react';
import { InputBaseComponentProps, Menu, ListItem, ListItemText } from '@material-ui/core';
import TextFieldButton from './TextFieldButton';

let gidCounter = 0;

const TextFieldWithMenu = React.memo(function TextFieldWithMenu(props: {
  size?: 'small' | 'medium';
  label: string;
  tooltip: string;
  inputProps?: InputBaseComponentProps;
  icon: React.ReactNode,
  buttonPosition?: 'start' | 'end';
  disabled?: boolean;
  value?: string;
  menuItems: [string, string][];
  onSelect?: (s: string) => void;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
}) {
  const [gid] = useState(() => `folder-textfield-button-${++gidCounter}`);
  const { size, label, tooltip, inputProps, icon, buttonPosition, disabled, value, menuItems, onSelect, onChange } = props;
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement | null>(null);
  const handleClick = useCallback((e: React.MouseEvent<HTMLButtonElement>) => setAnchorEl(e.currentTarget), []);
  const close = useCallback(() => setAnchorEl(null), [setAnchorEl]);
  const select = useCallback((selected: string) => {
    if (onSelect) {
      onSelect(selected);
    }
    close();
  }, [onSelect, close]);
  const items = useMemo(() => menuItems.map(([caption, value]) => (
    <ListItem component="li" button key={value} onClick={() => select(value)}>
      <ListItemText primary={caption} />
    </ListItem>
  )), [menuItems, select]);
  return (
    <>
      <TextFieldButton
        size={size}
        label={label}
        tooltip={tooltip}
        inputProps={inputProps}
        icon={icon}
        buttonPosition={buttonPosition}
        disabled={disabled}
        value={value}
        onClick={handleClick}
        onChange={onChange}
      />
      <Menu id={gid} anchorEl={anchorEl} getContentAnchorEl={null} anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }} keepMounted open={Boolean(anchorEl)} onClose={close}>
        {items}
      </Menu>
    </>
  );
});
export default TextFieldWithMenu;