import React, { useState, useCallback, useMemo } from 'react';
import { Button,  Menu, ListItem, ListItemText } from '@material-ui/core';
import * as Templates from './Templates';
import { TCharacterTemplate } from './Types';

let gidCounter = 0;

const AddCharacterButton = React.memo(function AddCharacterButton(props: {
  appName: string;
  disabled?: boolean;
  onAdd: (c: TCharacterTemplate) => void;
}) {
  const { appName, disabled, onAdd } = props;
  const [gid] = useState(() => `add-character-button-${++gidCounter}`);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = useCallback((event: React.MouseEvent<HTMLButtonElement>) => setAnchorEl(event.currentTarget), [setAnchorEl]);
  const close = useCallback(() => setAnchorEl(null), [setAnchorEl]);
  const select = useCallback((c: TCharacterTemplate) => {
    onAdd(c);
    close();
  }, [close, onAdd]);
  const templates = useMemo(() => Templates.findAll([appName]).reduce<TCharacterTemplate[]>((r, a) => { r.push(...a.characterTemplates); return r }, []), [appName]);
  const items = useMemo(() => templates.map(o => (
    <ListItem component="li" button key={o.name} onClick={() => select(o)}>
      <ListItemText primary={o.name} />
    </ListItem>
  )), [templates, select]);
  return (
    <>
      <Button variant="contained" color="primary" aria-controls={gid} aria-haspopup="true" disabled={disabled} onClick={open}>キャラクターを追加</Button>
      <Menu id={gid} anchorEl={anchorEl} getContentAnchorEl={null} anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }} keepMounted open={!!anchorEl} onClose={close}>
        {items}
      </Menu>
    </>
  );
});
export default AddCharacterButton;