import { Typography } from '@material-ui/core';
import React from 'react';
import StepperDialogButton from './StepperDialogButton';
import FileImage from './images/bouyomi_file.png';
import ConfigImage from './images/bouyomi_config.png';
import SaveImage from './images/bouyomi_save.png';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    '「音声 テキスト出力」プラグインをダウンロードする',
    <>
      <Typography>
        <a href="https://github.com/yukimaru73/Plugin_Bouyomichan_wav_and_txt/releases" target="_blank" rel="noreferrer">Plugin_Bouyomichan_wav_and_txt.dll</a> をダウンロードします。<br />
        このプラグインを導入すると、Wave ファイルとテキストファイルを同時に保存できるようになります。
      </Typography>
    </>
  ],
  [
    'プラグインを配置する',
    <>
      <img src={FileImage} alt="ファイルの配置例" width="669" height="533" />
      <Typography>
        ダウンロードした <Typography style={{ fontFamily: "monospace" }} display="inline">Plugin_Bouyomichan_wav_and_txt.dll</Typography> を、{title} と同じフォルダーに配置します。
      </Typography>
    </>
  ],
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    'プラグインを有効化し、設定画面を開く',
    <>
      <img src={ConfigImage} alt="起動画面例" width="314" height="418" />
      <Typography>
        「その他」タブを開き、その中にある「プラグイン」タブを開き、以下の操作を行います。
      </Typography>
      <ol>
        <li>「音声 テキスト出力」にチェックをつける</li>
        <li>「音声 テキスト出力」を選択する</li>
        <li>下にあるスパナアイコンをクリックして、プラグインの設定画面を開く</li>
      </ol>
    </>
  ],
  [
    `プラグインを設定する`,
    <>
      <img src={SaveImage} alt="保存先設定例" width="634" height="220" />
      {
        tmpPath ?
          <>
            <Typography>
              「01)ファイルの保存場所」を、<Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> に設定します。
            </Typography>
          </>
          :
          <>
            <Typography>
              「01)ファイルの保存場所」を、forcepser.exe（かんしくんのメインプログラム）と同じフォルダー内にある「tmp」フォルダーに設定します。
            </Typography>
            <Typography variant="body2">
              ※もし「tmp」フォルダーが見つからない場合は、手動で作成してください。
            </Typography>
          </>
      }
    </>,
  ],
  [
    'ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、ダイアログを閉じます。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonBouyomi(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
