import { Typography } from '@material-ui/core';
import React from 'react';
import ConfigImage from './images/yck_config.png';
import TestImage from './images/yck_test.png';
import MenuImage from './images/yck_menu.png';
import SoftalkImage from './images/yck_softalk.png';
import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
      <Typography variant="body2">
        ※ここでは、かんしくん と {title} を組み合わせて使うために必要な設定だけを紹介します。<br />
        事前に {title} が正しく発話できるよう設定しておいてください。
      </Typography>
    </>,
  ],
  [
    '「設定」タブで設定を行う',
    <>
      <img src={ConfigImage} alt="設定例" width="756" height="522" />
      <Typography>
        「設定」タブを開き、赤枠内を以下のように設定します。
      </Typography>
      <ol>
        <li>「出力テキスト文字コード」を「Shift_JIS」にする</li>
      </ol>
    </>,
  ],
  [
    '発話テストをする',
    <>
      <img src={TestImage} alt="発話テスト例" width="756" height="406" />
      <Typography>
        「ゆっくりしていってね（音声テスト）」ボタンを押して、SofTalk を起動します。
      </Typography>
    </>,
  ],
  [
    'SofTalk の環境設定を開く',
    <>
      <img src={MenuImage} alt="メニュー選択例" width="232" height="134" />
      <Typography>
        SofTalk のメインメニューから［オプション(O)］→［環境設定(S)］を選択します。
      </Typography>
    </>,
  ],
  [
    '「録音」タブを設定する',
    <>
      <img src={SoftalkImage} alt="設定例" width="736" height="244" />
      <Typography>
        「設定」ダイアログで「録音」タブを開き、赤枠内を以下のように設定します。
      </Typography>
      <ol>
        <li>
          「録音時にテキストを自動保存する」のチェックを外します。<br />
          ※テキストファイルはゆっくり調声くん側で作成されるため、この設定は無効にしておく必要があります。
        </li>
      </ol>
    </>,
  ],
  [
    '「設定」ダイアログを閉じる',
    <>
      <Typography>
        「閉じる」ボタンを押して、Softalk の「設定」ダイアログを閉じます。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonYCK(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
