import React, { useState, useCallback, useReducer, useEffect } from 'react';
import { Box, Container, Grid } from '@material-ui/core';
import AddApplicationButton from './AddApplicationButton';
import './App.css';
import { Application, TActions as TApplicationActions } from './Application';
import SwapAnimator from './SwapAnimator';
import SettingDialogButton from './SettingDialogButton';
import OverwriteGuardDialog from './OverwriteGuardDialog';
import { TApplication, TApplicationTemplate } from './Types';
import * as ForcepserConfig from './ForcepserConfig';

let gidCounter = 0;

type AppsAction = { type: 'load', apps: TApplication[] } |
{ type: 'add', v: TApplicationTemplate } |
  TApplicationActions;
const AppsReducer: React.Reducer<TApplication[], AppsAction> = (state, a) => {
  switch (a.type) {
    case 'load':
      return a.apps.map(a => {
        a.gkey = ++gidCounter;
        a.characters = a.characters.map(c => {
          c.gkey = ++gidCounter;
          return c;
        });
        return a;
      });
    case 'add':
      const app: TApplication = { ...a.v, gkey: ++gidCounter, characters: [] };
      if (a.v.characterTemplates.length === 1 && a.v.characterTemplates[0].onlyOne) {
        app.characters.push({ ...a.v.characterTemplates[0], layer: 1, gkey: ++gidCounter });
      }
      delete (app as any).characterTemplates;
      delete (app as any).stepperSetUp;
      return [...state, app];
    case 'move-up':
      {
        const r = [...state];
        [r[a.index], r[a.index - 1]] = [r[a.index - 1], r[a.index]];
        return r;
      }
    case 'move-down':
      {
        const r = [...state];
        [r[a.index], r[a.index + 1]] = [r[a.index + 1], r[a.index]];
        return r;
      }
    case 'delete':
      {
        const r = [...state];
        r.splice(a.index, 1);
        return r;
      }
    case 'update':
      {
        const r = [...state];
        r[a.index] = { ...r[a.index], ...a.v };
        return r;
      }
    case 'add-character':
      {
        const r = [...state];
        r[a.index] = { ...r[a.index], characters: [...r[a.index].characters, { ...a.v, layer: 1, gkey: ++gidCounter }] };
        return r;
      }
    case 'character':
      {
        const r = [...state];
        const aa = a.action;
        const c = r[a.index].characters = [...r[a.index].characters];
        switch (aa.type) {
          case 'move-up':
            [c[aa.index], c[aa.index - 1]] = [c[aa.index - 1], c[aa.index]];
            break;
          case 'move-down':
            [c[aa.index], c[aa.index + 1]] = [c[aa.index + 1], c[aa.index]];
            break;
          case 'delete':
            c.splice(aa.index, 1);
            break;
          case 'update':
            c[aa.index] = { ...c[aa.index], ...aa.v };
            break;
        }
        r[a.index] = { ...r[a.index], characters: c };
        return r;
      }
  }
}

const version = `${process.env.REACT_APP_TAG ? process.env.REACT_APP_TAG : "vX.X.X"} ( ${process.env.REACT_APP_HASH ? process.env.REACT_APP_HASH : "unknown"} )`;

const App = () => {
  const [initialized, setInitialized] = useState(false);
  const [exeVersion, setExeVersion] = useState(`HTML: ${version}`);
  const [fileState, setFileState] = useState<ForcepserConfig.TFileState>("notfound");
  const [apps, updateApps] = useReducer(AppsReducer, []);
  const [swappingKeys, setSwappingKeys] = useState<string[]>([]);
  const onClearSwapping = useCallback(() => setSwappingKeys([]), [setSwappingKeys]);
  const onAddApplication = useCallback((v: TApplicationTemplate) => updateApps({ type: 'add', v }), [updateApps]);
  useEffect(() => {
    if (initialized) {
      return;
    }
    ForcepserConfig.getFileState().then(([apps, fileState]) => {
      updateApps({ type: "load", apps: apps });
      setFileState(fileState);
      setInitialized(true);
    });
    if (ForcepserConfig.hasAPI()) {
      ForcepserConfig.version().then(v => setExeVersion(`EXE: ${v} / HTML: ${version}`));
    }
  }, [initialized]);
  useEffect(() => {
    if (!initialized || fileState === "noapi" || fileState === "unspecified" || fileState === "modified") {
      return;
    }
    ForcepserConfig.write(apps);
  }, [initialized, fileState, apps]);

  return (
    <Container>
      {initialized && (fileState === "unspecified" || fileState === "modified") && <OverwriteGuardDialog modified={fileState === "modified"} />}
      {initialized && (fileState === "safe" || fileState === "noapi" || fileState === "notfound") ? <>
        <SwapAnimator onStart={setSwappingKeys} onEnd={onClearSwapping}>
          {apps.map((app, index) => (
            <Application
              ref={React.createRef()}
              key={app.gkey}
              app={app}
              index={index}
              length={apps.length}
              swapping={swappingKeys.includes(`${app.gkey}`)}
              dispatch={updateApps}
            />
          ))}
        </SwapAnimator>
        <Grid container direction="row" justify="space-between" alignItems="flex-start">
          <Grid item>
            <AddApplicationButton onAdd={onAddApplication} />
          </Grid>
          <Grid item>
            {fileState === "noapi" ? <SettingDialogButton apps={apps} /> : undefined}
          </Grid>
        </Grid>
      </> : undefined}
      <Box my={2}>
        <span style={{ fontSize: "0.25rem" }}>バージョン情報 {exeVersion}</span>
      </Box>
    </Container>
  );
}

export default App;
