const extractFileName = (s: string) => {
  const m = s.match(/[^\\]+$/);
  if (m) {
    return m[0];
  }
  return "*.exe";
};

const aiTalkApplication = (productName: string, characterName: string, path: string, nickName: string): TApplicationTemplate => ({
  name: productName,
  exeName: extractFileName(path),
  bin: `C:\\Program Files (x86)\\${path}`,
  file: `${nickName}_*.wav`,
  folder: "%TEMPDIR%",
  modifiers: ['aiTalkRuby'],
  asasMode: 'autosave',
  editableCommonDir: false,
  commonDir: '%TEMPDIR%',
  characterTemplates: [{
    onlyOne: true,
    name: characterName,
    editableName: false,
    nickName: nickName,
    file: `${nickName}_*.wav`,
    editableFile: false,
    encoding: "sjis",
    dir: "",
    editableDir: false,
    useCommonDir: true,
    preset: "",
    detectType: 'file',
    modifiers: ['aiTalkRuby'],
  }],
  stepperSetUp: <StepperSetUpButtonAITalk title={productName} />,
});
