
import React, { useState, useEffect, useMemo } from 'react';
import { TextField, Button, Dialog, DialogTitle, DialogContent, Tooltip } from '@material-ui/core';
import { Visibility as PreviewIcon, GetApp as DownloadIcon } from '@material-ui/icons';
import { TApplication } from './Types';
import * as ForcepserConfig from './ForcepserConfig';

const monospaceStyle = { style: { fontFamily: 'monospace' } };
let gidCounter = 0;

export default function SettingDialogButton({
  apps,
}: {
  apps: TApplication[];
}) {
  const [gid] = useState(() => `setting-txt-button-${++gidCounter}`);
  const [download, setDownload] = useState(false);
  const [open, setOpen] = useState(false);
  // eslint-disable-next-line
  const content = useMemo(() => ForcepserConfig.toString(apps), [open, download]);
  const handlePreviewClick = () => setOpen(true);
  const handlePreviewClose = () => setOpen(false);
  const handleDownloadClick = () => setDownload(true);
  useEffect(() => {
    if (download) {
      const blob = new Blob([content], { type: "text/plain" });
      const filename = "setting.txt";
      if (window.navigator.msSaveBlob) {
        window.navigator.msSaveBlob(blob, filename);
      } else {
        const a = document.createElement('a');
        a.download = filename;
        const url = URL.createObjectURL(blob);
        a.href = url;
        a.dispatchEvent(new MouseEvent("click"));
        URL.revokeObjectURL(url);
      }
      setDownload(false);
    }
  }, [content, download]);
  return (
    <>
      <Tooltip title="setting.txt の内容をプレビュー">
        <Button
          variant="contained"
          onClick={handlePreviewClick}
          aria-controls={open ? (gid + "-dialog") : undefined}
          aria-expanded={open ? 'true' : undefined}
        ><PreviewIcon /></Button>
      </Tooltip>
      <Tooltip title="setting.txt をダウンロード">
        <Button variant="contained" color="primary" onClick={handleDownloadClick}>
          <DownloadIcon />
        </Button>
      </Tooltip>
      <Dialog
        id={gid + "-dialog"}
        fullWidth
        maxWidth="lg"
        onClose={handlePreviewClose}
        aria-labelledby={gid + "-dialog-title"}
        open={open}
      >
        <DialogTitle id={gid + "-dialog-title"}>現在の設定内容</DialogTitle>
        <DialogContent>
          <TextField variant="outlined" label="setting.txt" multiline value={content} inputProps={monospaceStyle} fullWidth />
        </DialogContent>
      </Dialog>
    </>
  );
}
