import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/aitalk6_menu.png';
import SaveTabImage from './images/aitalk6_save.png';
import VoiceTabImage from './images/aitalk6_voice.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '「プロジェクト設定」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="299" height="118" />
      <Typography>
        エディターのメインメニューから［ツール(T)］→［プロジェクト設定(P)...］を選びます。
      </Typography>
    </>,
  ],
  [
    '「ボイス」タブを設定する',
    <>
      <img src={VoiceTabImage} alt="設定例" width="469" height="290" />
      <Typography>
        「ボイス」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「入力テキスト中でボイスプリセットタグを有効にする」にチェックをつける</li>
        <li>「プロンプト文字列」を「＞」に設定する</li>
        <li>「テキストファイルにボイスプリセットタグを出力する」にチェックをつける</li>
      </ol>
    </>,
  ],
  [
    '「音声ファイル保存」タブを設定する',
    <>
      <img src={SaveTabImage} alt="設定例" width="479" height="633" />
      <Typography>
        「音声ファイル保存」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>ファイル形式を「WAVE」「48000Hz 16bit PCM」にして、「ファイルヘッダー有り」にチェックをつける</li>
        <li>「テキストファイルを音声ファイルと一緒に保存する」にチェックを入れて、文字コードを「Shift-JIS(CP932)」にする</li>
        <li>「ファイル命名規則を指定して選択する」を選ぶ</li>
        <li>
          {
            tmpPath ?
              <>
                フォルダーを <Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> に設定する
              </>
              :
              <>
                フォルダーを forcepser.exe（かんしくんのメインプログラム）と同じフォルダー内にある「tmp」フォルダーに設定する
                <Typography variant="body2">
                  ※もし「tmp」フォルダーが見つからない場合は、手動で作成してください。
                </Typography>
              </>
          }
        </li>
        <li>「命名規則」を <Typography style={{ fontFamily: "monospace" }} display="inline">{"{yyyyMMdd}{HHmmss}_{VoicePreset}_{Text}"}</Typography> に設定する</li>
      </ol>
    </>,
  ],
  [
    '「プロジェクト設定」ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、「プロジェクト設定」ダイアログを閉じます。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonAITalk6(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}