
import React, { useState } from 'react';
import { Box, Dialog, DialogTitle, DialogContent, Typography } from '@material-ui/core';

let gidCounter = 0;

export default function OverwriteGuardDialog(props: {modified?: boolean}) {
  const { modified } = props;
  const [gid] = useState(() => `overwrite-guard-dialog-${++gidCounter}`);
  const [open, setOpen] = useState(true);
  const handlePreviewClose = () => setOpen(false);
  return (
    <>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        id={gid + "-dialog"}
        fullWidth
        maxWidth="lg"
        onClose={handlePreviewClose}
        aria-labelledby={gid + "-dialog-title"}
        open={open}
      >
        <DialogTitle id={gid + "-dialog-title"}>
          設定ファイルの編集ができません
        </DialogTitle>
        <DialogContent>
          <Typography>
            {modified ? <>
              現在の設定ファイルには<Typography color="error" display="inline">自動生成されたファイルを書き換えた形跡がある</Typography>ため編集できません。
            </> : <>
              現在の設定ファイルが<Typography color="error" display="inline">このツールで作成されたものではない</Typography>ため編集できません。
            </>}
            <Box my={2}>
              このツールは既存の設定ファイルを完全に破壊してしまうため、保護機能によって編集がブロックされています。
            </Box>
            <Box my={2}>
              このツールを使うには<Typography color="error" display="inline">現在の設定ファイルを手動で削除してから</Typography>ツールを起動してください。
            </Box>
          </Typography>
        </DialogContent>
      </Dialog>
    </>
  );
}
