import React, { useState, useCallback, useMemo } from 'react';
import { Box, Grid, Card, CardHeader, CardContent, Collapse, Typography } from '@material-ui/core';
import * as ActionButtons from './ActionButtons';
import { TCharacter, TCharacterPartial } from './Types';
import MemoizedTextField from './MemoizedTextField';
import TextFieldWithMenu from './TextFieldWithMenu';
import { Folder as FolderIcon } from '@material-ui/icons';

export type TActions =
  { type: 'move-up' | 'move-down' | 'delete', index: number } |
  { type: 'update', index: number, v: TCharacterPartial };

const date = () => {
  const n = new Date();
  const y = n.getFullYear();
  const m = '0' + (n.getMonth() + 1);
  const d = '0' + n.getDay();
  const h = '0' + n.getHours();
  const i = '0' + n.getMinutes();
  const s = '0' + n.getSeconds();
  return y + m.substring(m.length - 2) + d.substring(d.length - 2) + '_' + h.substring(h.length - 2) + i.substring(i.length - 2) + s.substring(s.length - 2);
};

const talkExamples = [
  'こんにちは',
  'ありがとう',
  'わかる',
  'なるほどね',
  'ホントに？',
  'すごいね',
  'いいね',
  '素晴らしい',
  '天才かな？',
  '面白いね',
  'やるじゃん',
  'やるやんけ',
  '上手いね',
  '詳しいね',
  '心強いね',
  '諸行無常',
];

const mosaics = '░▒▓█';
const getMosaic = (length: number) => {
  const r: string[] = [];
  for (let i = 0; i < length; ++i) {
    r.push(mosaics.charAt(Math.floor(Math.random() * mosaics.length)));
  }
  return r.join('');
};

const specialFolders: [string, string][] = [
  ['かんしくんと同じ場所にある tmp フォルダー', '%TEMPDIR%'],
  ['AviUtl で編集中のプロジェクトファイルがあるフォルダー', '%PROJECTDIR%'],
  ['C:\\Users\\あなたのユーザー名', '%PROFILE%'],
  ['デスクトップ', '%DESKTOP%'],
  ['ドキュメント', '%MYDOC%'],
];
const folderIcon = <FolderIcon />;

type Props = {
  appName: string;
  character: TCharacter;
  index: number;
  length: number;
  disabled?: boolean;
  dispatch: React.Dispatch<TActions>;
  swapping: boolean;
}

const layerRange = { min: 1, max: 100 };
const layerLabelProps = { shrink: true };
const monospaceStyle = { style: { fontFamily: 'monospace' } };

export const Character = React.forwardRef<HTMLDivElement, Props>(function Character(props, ref) {
  const { appName, character, index, length, disabled, dispatch, swapping } = props;
  const [collapsed, setCollapsed] = useState(() => character.onlyOne ? false : true);
  const [animating, setAnimating] = useState(false);
  const [animated, setAnimated] = useState(() => character.onlyOne ? true : false);
  const messageExample = useMemo(() => `最終的に保存されるファイル名は「${date()}_${character.nickName}_${talkExamples[Math.floor(Math.random() * talkExamples.length)]}.wav」のようなものになります`, [character.nickName]);
  const nameHelperText = useMemo(() => `${appName} が「${getMosaic(4)}_${character.name}_${getMosaic(4)}.wav」のような名前でファイルを保存したときに反応します`, [appName, character.name]);
  const nameStyleHelperText = useMemo(() => `${appName} が「${getMosaic(4)}_${character.name}_${getMosaic(4)}.wav」または「${getMosaic(4)}_${character.name}（${getMosaic(4)}）_${getMosaic(4)}.wav」のような名前でファイルを保存したときに反応します`, [appName, character.name]);
  const fileHelperText = useMemo(() => `${appName} が「${character.file.replace(/\*/g, getMosaic.bind(null, 4))}」のような名前でファイルを保存したときに反応します`, [appName, character.file]);
  const handleActionButtons = useCallback((a: ActionButtons.TActions) => {
    if (a.type === "delete") {
      setAnimated(false);
    } else {
      dispatch({ type: a.type, index });
    }
  }, [dispatch, index]);
  const handleChangeName = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { name: e.target.value } }), [dispatch, index]);
  const handleChangeFile = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { file: e.target.value } }), [dispatch, index]);
  const handleChangeLayer = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { layer: parseInt(e.target.value, 10) } }), [dispatch, index]);
  const handleChangeDir = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { dir: e.target.value } }), [dispatch, index]);
  const handleChangeDirFromTemplate = useCallback((s: string) => dispatch({ type: 'update', index, v: { dir: s } }), [dispatch, index]);
  const handleChangePreset = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { preset: e.target.value } }), [dispatch, index]);
  const handleChangeNickName = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { nickName: e.target.value } }), [dispatch, index]);
  const handleCollapseAnimating = useCallback(() => setAnimating(true), []);
  const handleCollapseEntered = useCallback(() => setAnimating(false), []);
  const handleCollapseExited = useCallback(() => dispatch({ type: "delete", index }), [dispatch, index]);
  React.useEffect(() => {
    if (!animated) {
      setCollapsed(f => !f);
      setAnimated(true);
    }
  }, [animated]);

  return (
    <div ref={ref}>
      <Collapse in={!collapsed} onExiting={handleCollapseAnimating} onEntering={handleCollapseAnimating} onEntered={handleCollapseEntered} onExited={handleCollapseExited}>
        <Box my={2}>
          <Card variant="outlined">
            <CardHeader
              action={character.onlyOne ? undefined : (
                <ActionButtons.ActionButtons
                  name={`キャラクター「${character.name}」`}
                  index={index}
                  length={length}
                  disabled={disabled || animating || swapping}
                  dispatch={handleActionButtons}
                />
              )}
              title={<Typography variant="h6" component="h3">{character.name}</Typography>}
              disableTypography={true}
            />
            <CardContent>
              <form noValidate autoComplete="off">
                <Grid container spacing={3}>
                  <Grid item xs={4}>
                    <MemoizedTextField
                      size="small"
                      type="number"
                      label="レイヤー"
                      variant="outlined"
                      disabled={disabled || animating || swapping}
                      value={character.layer}
                      onChange={handleChangeLayer}
                      inputProps={layerRange}
                      InputLabelProps={layerLabelProps}
                      fullWidth
                    />
                  </Grid>
                  {!character.editableName ? undefined : (
                    <Grid item xs={12}>
                      <MemoizedTextField
                        size="small"
                        label="ターゲットになるキャラクター名"
                        variant="outlined"
                        disabled={disabled || animating || swapping}
                        value={character.name}
                        onChange={handleChangeName}
                        helperText={character.detectType === 'voiceVoxStyle' ? nameStyleHelperText : nameHelperText}
                        fullWidth
                      />
                    </Grid>
                  )}
                  {!character.editableFile ? undefined : (
                    <Grid item xs={12}>
                      <MemoizedTextField
                        size="small"
                        label="ターゲットになるファイル名"
                        variant="outlined"
                        disabled={disabled || animating || swapping}
                        value={character.file}
                        onChange={handleChangeFile}
                        helperText={fileHelperText}
                        fullWidth
                      />
                    </Grid>
                  )}
                  {!character.editableDir ? undefined : (
                    <Grid item xs={12}>
                      <TextFieldWithMenu
                        size="small"
                        label="監視フォルダー"
                        tooltip="特殊フォルダーをテンプレートから入力"
                        disabled={disabled || animating || swapping}
                        inputProps={monospaceStyle}
                        icon={folderIcon}
                        menuItems={specialFolders}
                        value={character.dir}
                        onChange={handleChangeDir}
                        onSelect={handleChangeDirFromTemplate}
                      />
                    </Grid>
                  )}
                  {/* for VOICEROID2 and AI Talk only */}
                  {(character.detectType !== 'voicePresetText' && character.detectType !== 'voicePresetFile') ? undefined : (
                    <Grid item xs={12}>
                      <MemoizedTextField
                        size="small"
                        label="プリセット名"
                        variant="outlined"
                        disabled={disabled || animating || swapping}
                        value={character.preset}
                        onChange={handleChangePreset}
                        helperText={`「${character.preset}」だけでなく「${character.preset} - 喜び」のようなプリセット名にも反応します`}
                        fullWidth
                      />
                    </Grid>
                  )}
                  <Grid item xs={12}>
                    <MemoizedTextField
                      size="small"
                      label="最終保存時のキャラクター名"
                      variant="outlined"
                      disabled={disabled || animating || swapping}
                      value={character.nickName}
                      onChange={handleChangeNickName}
                      helperText={messageExample}
                      fullWidth
                    />
                  </Grid>
                </Grid>
              </form>
            </CardContent>
          </Card>
        </Box>
      </Collapse>
    </div>
  );
});
