import React, { useState, useLayoutEffect } from "react";

type RefKey = { ref: React.MutableRefObject<HTMLElement>, key: string };
const isRefKey = (v: any): v is RefKey => v && v.ref && v.key;

const getKeys = (children: React.ReactNode): string[] => {
  const r: string[] = [];
  React.Children.forEach(children, child => {
    if (isRefKey(child) && child.ref.current) {
      r.push(child.key);
    }
  });
  return r;
};

const getElements = (children: React.ReactNode): HTMLElement[] => {
  const r: HTMLElement[] = [];
  React.Children.forEach(children, child => {
    if (isRefKey(child) && child.ref.current) {
      r.push(child.ref.current);
    }
  });
  return r;
};

const SwapAnimator = function SwapAnimator(props: {
  children: React.ReactNode;
  onStart?: (keyPair: string[]) => void,
  onEnd?: (keyPair: string[]) => void,
}) {
  const [prevKeys, setPrevKeys] = useState<string[]>([]);
  const { children, onStart, onEnd } = props;
  useLayoutEffect(() => {
    const keys = getKeys(children);
    if (keys.length === prevKeys.length && keys.join("\t") !== prevKeys.join("\t")) {
      const elems = getElements(children);
      for (let i = 0; i < elems.length; ++i) {
        if (keys[i] !== prevKeys[i]) {
          const e1 = elems[i];
          const e2 = elems[i + 1];
          const bb1 = e1.getBoundingClientRect();
          const bb2 = e2.getBoundingClientRect();
          const margin = bb2.top - (bb1.top + bb1.height);
          e1.style.transition = 'transform 0ms';
          e1.style.transform = `translateY(${Math.floor(bb2.height + margin)}px)`;
          e2.style.transition = 'transform 0ms';
          e2.style.transform = `translateY(-${Math.floor(bb1.height + margin)}px)`;
          if (onStart) {
            onStart([keys[i], prevKeys[i]]);
          }
          setTimeout(() => {
            const f = (e:TransitionEvent) => {
              if (e.target === e1) {
                if (onEnd) {
                  onEnd([keys[i], prevKeys[i]]);
                }
                e1.removeEventListener('transitionend', f);
              }
            };
            e1.addEventListener('transitionend', f);
            e1.style.transition = 'transform 350ms';
            e1.style.transform = "translateY(0)";
            e2.style.transition = 'transform 350ms';
            e2.style.transform = "translateY(0)";
          }, 0);
          break;
        }
      }
    }
    setPrevKeys(keys);
  }, [children, onStart, onEnd]); // eslint-disable-line
  return children as React.ReactElement;
};

export default SwapAnimator;
