import React, { useState } from 'react';
import { Button, Menu, List, ListItem, ListItemText, ListSubheader } from '@material-ui/core';
import * as Templates from './Templates';
import { TApplicationTemplate } from './Types';

let gidCounter = 0;

const AddApplicationButton = React.memo(function AddApplicationButton(props: {
  disabled?: boolean;
  onAdd: (selected: TApplicationTemplate) => void;
}) {
  const { disabled, onAdd } = props;
  const [templates] = useState(() => Templates.templates);
  const [gid] = useState(() => `add-application-button-${++gidCounter}`);
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement | null>(null);
  const open = (event: React.MouseEvent<HTMLButtonElement>) => setAnchorEl(event.currentTarget);
  const close = () => setAnchorEl(null);
  const select = (app: TApplicationTemplate) => {
    onAdd(app);
    close();
  };
  return (
    <>
      <Button variant="contained" color="secondary" aria-controls={gid} aria-haspopup="true" disabled={disabled} onClick={open}>アプリケーションを追加</Button>
      <Menu id={gid} anchorEl={anchorEl} getContentAnchorEl={null} anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }} keepMounted open={Boolean(anchorEl)} onClose={close}>
        {templates.map(o => (
          <li key={o.name}>
            <List component="ul" dense disablePadding>
              <ListSubheader component="li" disableSticky>{o.name}</ListSubheader>
              {o.items.map(o => (
                <ListItem component="li" button key={o.name} style={{ paddingLeft: '2rem' }} onClick={() => select(o)}>
                  <ListItemText primary={o.name} />
                </ListItem>
              ))}
            </List>
          </li>
        ))}
      </Menu>
    </>
  );
});
export default AddApplicationButton;