import React, { useState } from 'react';
import { Box, Stepper, Step, StepLabel, StepContent, Button, Dialog, DialogTitle, DialogContent } from '@material-ui/core';
import { KeyboardArrowDown as NextIcon, KeyboardArrowUp as PrevIcon, Done as FinishIcon } from '@material-ui/icons';

let gidCounter = 0;

export default function StepperDialogButton(props: {
  title: string;
  caption?: string;
  disabled?: boolean;
  steps: [string, React.ReactNode][];
}) {
  const { title, caption, disabled, steps } = props;
  const [gid] = useState(() => `stepper-dialog-button-${++gidCounter}`);
  const [open, setOpen] = useState(false);
  const handleButtonClick = () => { setStep(0); setOpen(true); }
  const handleClose = () => setOpen(false);
  const [step, setStep] = useState(0);
  const handleNext = () => setStep(prev => prev + 1);
  const handleBack = () => setStep(prev => prev - 1);
  return (
    <>
      <Button variant="contained" disabled={disabled} onClick={handleButtonClick}>{caption ? caption : title}</Button>
      <Dialog fullWidth maxWidth="lg" onClose={handleClose} aria-labelledby={gid} open={open}>
        <DialogTitle id={gid}>{title}</DialogTitle>
        <DialogContent>
          <Stepper activeStep={step} orientation="vertical">
            {steps.map(([label, content]) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
                <StepContent>
                  {content}
                  <Box my={2}>
                    <Button
                      variant="contained"
                      disabled={step === 0}
                      onClick={handleBack}
                    >
                      <PrevIcon />前へ
                  </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={step === steps.length - 1 ? handleClose : handleNext}
                    >
                      {step === steps.length - 1 ? (<><FinishIcon />完了</>) : (<><NextIcon />次へ</>) }
                    </Button>
                  </Box>
                </StepContent>
              </Step>
            ))}
          </Stepper>
        </DialogContent>
      </Dialog>
    </>
  );
}
