import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/cevio_menu.png';
import TalkImage from './images/cevio_talk.png';
import Talk2Image from './images/cevio_talk2.png';
import Menu2Image from './images/cevio_menu2.png';
import ExportImage from './images/cevio_export.png';
import Menu3Image from './images/cevio_menu3.png';
import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '「オプション」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="226" height="81" />
      <Typography>
        エディターのメインメニューから［ツール(T)］→［オプション(O)...］を選びます。
      </Typography>
    </>,
  ],
  [
    '「トーク設定」タブを設定する',
    <>
      <img src={TalkImage} alt="設定例" width="697" height="415" />
      <Typography>
        「トーク設定」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「テキストファイルの文字コード」枠内にある「書き出し」を「Shift JIS」に設定する</li>
      </ol>
    </>,
  ],
  [
    '「オプション」ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、「オプション」ダイアログを閉じます。
      </Typography>
    </>,
  ],
  [
    'トークボイスを選択し、適当なセリフを入力する',
    <>
      <img src={Talk2Image} alt="入力例" width="522" height="336" />
      <Typography>
        内容は何でもいいので適当なキャラクターで適当なセリフを入力します。
      </Typography>
    </>,
  ],
  [
    '「セリフの連続WAV書き出し」を選択する',
    <>
      <img src={Menu2Image} alt="選択例" width="525" height="295" />
      <Typography>
        エディターのメインメニューから［ファイル(F)］→［エクスポート(E)］→［セリフの連続WAV書き出し(W)...］を選びます。
      </Typography>
    </>,
  ],
  [
    '「セリフの連続WAV書き出し」ダイアログを設定する',
    <>
      <img src={ExportImage} alt="設定例" width="468" height="372" />
      <Typography>
        上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>ファイル名パターンを　<Typography style={{ fontFamily: "monospace" }} display="inline">$通番$_$キャスト名$_$セリフ$.wav</Typography> に設定する</li>
        <li>「セリフをテキスト出力」にチェックをつける</li>
        <li>設定を保存するため、OKボタンを押して適当なフォルダーへ音声ファイルをエクスポートする</li>
      </ol>
    </>,
  ],
  [
    '参考: セリフの保存方法について',
    <>
      <img src={Menu3Image} alt="選択例" width="613" height="587" />
      <Typography>
        セリフの保存には右クリックメニューの「WAV書き出し(W)...」を使用します。<br />
        Ctrl + W のショートカットキーも使用可能です。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonCeVIO(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
