import React, { useState, useMemo } from 'react';
import { FormControl, InputLabel, InputBaseComponentProps, FormHelperText, OutlinedInput, InputAdornment, IconButton, Tooltip } from '@material-ui/core';
import { MoreHoriz as DefaultIcon } from '@material-ui/icons';

let gidCounter = 0;

const TextFieldButton = React.memo(function TextFieldButton({
  size,
  label,
  tooltip,
  inputProps,
  icon,
  buttonPosition,
  hideIcon,
  disabled,
  error,
  helperText,
  value,
  onClick,
  onChange
}: {
  size?: 'small' | 'medium';
  label?: string;
  tooltip?: string;
  inputProps?: InputBaseComponentProps;
  icon?: React.ReactNode,
  buttonPosition?: 'start' | 'end';
  hideIcon?: boolean;
  disabled?: boolean;
  error?: boolean;
  helperText?: React.ReactNode;
  value?: string;
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
}) {
  const [gid] = useState(() => `textfield-button-${++gidCounter}`);
  const pos = buttonPosition === 'end' ? 'end' : 'start';
  const adornmentPos = useMemo(() => hideIcon ? {} : { [`${pos}Adornment`]: (
    <InputAdornment position={pos}>
      <Tooltip title={tooltip || ""}>
        <IconButton aria-label={tooltip} onClick={onClick}>
          {icon ? icon : <DefaultIcon />}
        </IconButton>
      </Tooltip>
    </InputAdornment>
  ) }, [pos, tooltip, icon, onClick, hideIcon]);
  return (
    <FormControl fullWidth variant="outlined" size={size} disabled={disabled}>
      <InputLabel htmlFor={gid} error={error}>{label}</InputLabel>
      <OutlinedInput
        id={gid}
        {...adornmentPos}
        label={label}
        value={value}
        onChange={onChange}
        error={error}
        inputProps={inputProps}
      />
      {helperText ? <FormHelperText error={error}>{helperText}</FormHelperText> : undefined}
    </FormControl>
  )
});
export default TextFieldButton;
