import React from 'react';
import { ThemeProvider, ThemeOptions, unstable_createMuiStrictModeTheme as createMuiTheme } from '@material-ui/core/styles';

const lightTheme: ThemeOptions = {
  palette: {
    type: "light",
    primary: {
      main: '#1565c0',
    },
    secondary: {
      main: '#e91e63',
    },
  },
  overrides: {
    MuiCssBaseline: {
      '@global': {
        '*::-webkit-scrollbar': {
          width: '14px',
          height: '14px',
        },
        '*::-webkit-scrollbar-button': {
          backgroundImage: `url("data:image/svg+xml;utf8,<svg version='1.1' viewBox='0 0 16 16' fill='rgba(0, 0, 0, 0.3)' xmlns='http://www.w3.org/2000/svg'><circle cx='8' cy='8' r='2'/></svg>")`,
        },
        '*::-webkit-scrollbar-button:hover': {
          backgroundColor: 'rgba(0, 0, 0, .0625)',
        },
        '*::-webkit-scrollbar-track': {
          backgroundColor: 'transparent',
        },
        '*::-webkit-scrollbar-track-piece': {
          backgroundColor: 'transparent',
        },
        '*::-webkit-scrollbar-thumb': {
          backgroundColor: 'rgba(0, 0, 0, .2)',
        },
        '*::-webkit-scrollbar-thumb:hover': {
          backgroundColor: 'rgba(0, 0, 0, .25)',
        },
        '*::-webkit-scrollbar-corner': {
          backgroundColor: 'rgba(0, 0, 0, .125)',
        },
      },
    },
  },
};

const darkTheme: ThemeOptions = {
  palette: {
    type: "dark",
    primary: {
      main: '#2196f3',
    },
    secondary: {
      main: '#ec407a',
    },
  },
  overrides: {
    MuiCssBaseline: {
      '@global': {
        '*::-webkit-scrollbar': {
          width: '14px',
          height: '14px',
        },
        '*::-webkit-scrollbar-button': {
          backgroundImage: `url("data:image/svg+xml;utf8,<svg version='1.1' viewBox='0 0 16 16' fill='rgba(255, 255, 255, 0.3)' xmlns='http://www.w3.org/2000/svg'><circle cx='8' cy='8' r='2'/></svg>")`,
        },
        '*::-webkit-scrollbar-button:hover': {
          backgroundColor: 'rgba(255, 255, 255, .0625)',
        },
        '*::-webkit-scrollbar-track': {
          backgroundColor: 'transparent',
        },
        '*::-webkit-scrollbar-track-piece': {
          backgroundColor: 'transparent',
        },
        '*::-webkit-scrollbar-thumb': {
          backgroundColor: 'rgba(255, 255, 255, .2)',
        },
        '*::-webkit-scrollbar-thumb:hover': {
          backgroundColor: 'rgba(255, 255, 255, .25)',
        },
        '*::-webkit-scrollbar-corner': {
          backgroundColor: 'rgba(255, 255, 255, .125)',
        },
      },
    },
  },
};

const Theme = ({ children }: { children?: React.ReactNode }) => {
  const [colorMode, setColorMode] = React.useState<ThemeOptions>(() => (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) ? darkTheme : lightTheme);
  React.useLayoutEffect(() => {
    if (window.matchMedia) {
      const f = (e: MediaQueryListEvent) => setColorMode(e.matches ? darkTheme : lightTheme);
      const m = window.matchMedia('(prefers-color-scheme: dark)');
      if (m.addEventListener && m.removeEventListener) {
        m.addEventListener('change', f);
        return () => m.removeEventListener('change', f);
      }
    }
  }, []);
  return (
    <ThemeProvider theme={createMuiTheme(colorMode)}>
      {children}
    </ThemeProvider>
  );
};
export default Theme;