import { TApplication } from './Types';
import { modifiers } from './Templates';

type TForcepserConfig = {
  read: () => Promise<{ text: string }>;
  write: (params: { text: string }) => Promise<{}>;
  exists: (params: { path: string }) => Promise<{ exists: boolean; }>;
  chooseFile: (params: { title: string; filename: string; path: string; filter: string; }) => Promise<{ path: string; }>;
  getTmpPath: () => Promise<{ path: string }>;
  version: () => Promise<{ version: string }>;
};

const encodeBase64 = (s: string): string => btoa(unescape(encodeURIComponent(s)));
const decodeBase64 = (s: string): string => decodeURIComponent(escape(atob(s)));

const api = (() => {
  const hostAPI: TForcepserConfig | undefined = (window as any).ForcepserConfig;
  if (hostAPI) {
    return hostAPI as TForcepserConfig;
  }
  return undefined;
})();

export type TFileState = "noapi" | "safe" | "unspecified" | "modified" | "notfound";

export function hasAPI(): boolean {
  return api !== undefined;
}

const hashSeed = 0xf7d93c8b;

// https://developer.mozilla.org/en/docs/Web/JavaScript/Reference/Global_Objects/Math/imul#Polyfill
const imulPolyfill = (a: number, b: number): number => {
  const ah = (a >>> 16) & 0xffff;
  const al = a & 0xffff;
  const bh = (b >>> 16) & 0xffff;
  const bl = b & 0xffff;
  // the shift by 0 fixes the sign on the high part
  // the final |0 converts the unsigned value into a signed value
  return al * bl + (((ah * bl + al * bh) << 16) >>> 0) | 0;
};
const imul = Math.imul ? Math.imul : imulPolyfill;

const numberTo32BitHex = (n: number): string => {
  const r = "0000000" + n.toString(16);
  return r.substring(r.length - 8);
};

// https://stackoverflow.com/a/52171480
const cyrb64 = (str: string, seed: number): string => {
  let h1 = 0xb407ed5c ^ seed, h2 = 0x41c6ce57 ^ seed;
  for (let i = 0, ch; i < str.length; i++) {
    ch = str.charCodeAt(i);
    h1 = imul(h1 ^ ch, 2654435761);
    h2 = imul(h2 ^ ch, 1597334677);
  }
  h1 = imul(h1 ^ (h1 >>> 16), 2246822507) ^ imul(h2 ^ (h2 >>> 13), 3266489909);
  h2 = imul(h2 ^ (h2 >>> 16), 2246822507) ^ imul(h1 ^ (h1 >>> 13), 3266489909);
  return numberTo32BitHex(h2 >>> 0) + numberTo32BitHex(h1 >>> 0);
};

export function toString(apps: TApplication[]): string {
  const r: string[] = [];
  r.push(`# 注意: このファイルの文字コードは必ず UTF-8 にしてください`);
  r.push(`# 設定の書き方: https://github.com/oov/forcepser/wiki/%E8%A8%AD%E5%AE%9A%E3%81%AB%E3%81%A4%E3%81%84%E3%81%A6(0.1beta15%E4%BB%A5%E9%99%8D)`);
  r.push(``);
  r.push(`# この設定ファイルは かんしくん v1.5 以降対応です。`);
  r.push(``);
  r.push(`# ==== グローバルセクション ====`);
  r.push(``);
  r.push(`# ◆ 音声ファイルを拡張編集に自動挿入したあと、後ろに確保する余白の大きさをミリ秒単位で指定`)
  r.push(`padding = 200`);
  r.push(``);
  r.push(`# ◆ 音声ファイルを拡張編集に投げ込む前に、音声ファイルを destdir で指定したフォルダーへ移動する`);
  r.push(`# 'off' だと移動しない、'move' だと移動する、'copy' だとコピーする`);
  r.push(`filemove = 'move'`);
  r.push(``);
  r.push(`# ◆ 最終的な音声ファイルの配置場所`);
  r.push(`# [注意！] これを変更すると AviUtl のプロジェクトファイルをフォルダーごと移動したときにファイルが正しく読み込まれなくなります。`);
  r.push(`destdir = '''%PROJECTDIR%'''`);
  r.push(``);
  r.push(`# ◆ 音声ファイルを拡張編集に投げ込む前に、テキストファイルを削除する`);
  r.push(`# true だと削除する、false だと削除しない`);
  r.push(`deletetext = true`);
  r.push(``);

  r.push(`# ==== [[asas]] セクション ====`);
  r.push(``);

  for (const a of apps) {
    if (a.asasMode === 'disable') {
      continue;
    }
    r.push(`# 「${a.name}」の連動起動`);
    r.push(`[[asas]]`);
    r.push(`exe = '''${a.bin}'''`);
    if (a.asasMode === 'autosave') {
      if (a.file) {
        r.push(`format = '''${a.file}'''`);
      }
    } else {
      r.push(`flags = 0`);
    }
    r.push(``);
  }

  r.push(`# ==== [[rule]] セクション ====`);
  r.push(``);

  for (const a of apps) {
    for (const c of a.characters) {
      r.push(`# 「${c.name}」のためのルール`);
      r.push(`[[rule]]`);
      r.push(`layer = ${c.layer}`);
      r.push(`encoding = '''${c.encoding}'''`);
      // If it is not editable and is blank, use the a.commonDir
      if (!c.editableDir && c.dir === '') {
        r.push(`dir = '''${a.commonDir}'''`);
      } else {
        r.push(`dir = '''${c.dir}'''`);
      }
      switch (c.detectType)
      {
        case 'voicePresetFile':
          r.push(`filere = '''^.+?_${c.preset.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}(?:| - .+?)_.*?\\.[Ww][Aa][Vv]$'''`);
          break;
        case 'voicePresetText':
          r.push(`file = '''${c.file}'''`);
          r.push(`text = '''^${c.preset.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}(?:| - .+?)＞'''`);
          break;
        case 'voiceVoxStyle':
          r.push(`filere = '''^.+?_${c.name.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}(?:|（.+?）)_.*?\\.[Ww][Aa][Vv]$'''`);
          break;
        case 'TALQu':
          r.push(`filere = '''^.+?_${c.name.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}_.*?\\.[Ww][Aa][Vv]$'''`);
          break;
        case 'file':
          r.push(`file = '''${c.file}'''`);
          break;
      }
      r.push(`modifier = '''`);
      for (const m of c.modifiers) {
        const mm = modifiers[m];
        r.push(`  -- ${mm[0]}`);
        r.push(`  ${mm[1]}`);
      }
      r.push(`  -- ファイル名を整形`);
      r.push(`  filename = os.date("%y%m%d_%H%M%S") .. [==[_${c.nickName}_]==] .. tofilename(text, 10) .. ".wav"`);
      r.push(`'''`);
      r.push(``);
    }
  }
  const txt = r.join('\r\n') + '\r\n# 以下の行は消さないでください\r\n';

  return txt +
    `# forcepserconfig:${encodeBase64(JSON.stringify(apps))}.${cyrb64(txt, hashSeed)}\r\n`;
}

export function write(apps: TApplication[]): Promise<undefined> {
  return new Promise(async (resolve, reject) => {
    if (!api) {
      reject("noapi");
      return;
    }
    try {
      await api.write({ text: toString(apps) });
      resolve(undefined);
    } catch (e) {
      reject(e);
    }
  });
}

export function fromString(s: string): TApplication[] | "unspecified" | "modified" {
  const m = s.match(/^# forcepserconfig:([A-Za-z0-9+/]+={0,3})(?:\.([A-Fa-f0-9]{16}))?$/m);
  if (!m) {
    return "unspecified";
  }
  if (m[2] !== undefined) {
    const h = cyrb64(s.substring(0, m.index), hashSeed);
    if (h !== m[2]) {
      return "modified";
    }
  }
  // TODO: verify json
  return JSON.parse(decodeBase64(m[1]));
}

export function read(): Promise<TApplication[]> {
  return new Promise(async (resolve, reject) => {
    if (!api) {
      reject("noapi");
      return;
    }
    try {
      const ret = await api.read();
      const r = fromString(ret.text);
      if (r === "unspecified" || r === "modified") {
        reject(r);
        return;
      }
      resolve(r);
    } catch (e) {
      reject("notfound");
      return;
    }
  });
}
export function getFileState(): Promise<[TApplication[], TFileState]> {
  return new Promise(async (resolve, reject) => {
    try {
      resolve([await read(), "safe"]);
    } catch (e) {
      if (e === "noapi" || e === "notfound" || e === "unspecified" || e === "modified") {
        resolve([[], e]);
      }
      reject(e);
    }
  });
}

export function exists(path: string): Promise<boolean> {
  return new Promise(async (resolve, reject) => {
    if (!api) {
      reject("noapi");
      return;
    }
    try {
      const r = await api.exists({ path });
      resolve(r.exists);
    } catch (e) {
      reject(e);
    }
  });
}

export function chooseFile(params: {
  path: string;
  filename: string;
  title: string;
  filter: string;
}): Promise<string> {
  return new Promise(async (resolve, reject) => {
    if (!api) {
      reject("noapi");
      return;
    }
    try {
      const { path, filename, title, filter } = params;
      const r = await api.chooseFile({ path, filename, title, filter });
      resolve(r.path);
    } catch (e) {
      reject(e);
    }
  });
}

export function getTmpPath(): Promise<string> {
  return new Promise(async (resolve, reject) => {
    if (!api) {
      reject("noapi");
      return;
    }
    try {
      const r = await api.getTmpPath();
      resolve(r.path);
    } catch (e) {
      reject(e);
    }
  });
}

export function version(): Promise<string> {
  return new Promise(async (resolve, reject) => {
    if (!api) {
      reject("noapi");
      return;
    }
    try {
      const r = await api.version();
      resolve(r.version);
    } catch (e) {
      reject(e);
    }
  });
}