import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/aitalk_menu.png';
import SaveImage from './images/aitalk_save.png';
import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '「音声出力設定」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="221" height="134" />
      <Typography>
        エディターのメインメニューから［設定(S)］→［音声出力設定(S)...］を選びます。
      </Typography>
    </>,
  ],
  [
    '設定する',
    <>
      <img src={SaveImage} alt="設定例" width="564" height="156" />
      <Typography>
        上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「テキストファイルを一緒に保存する」にチェックをつける</li>
      </ol>
    </>,
  ],
  [
    '「音声出力設定」ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、「音声出力設定」ダイアログを閉じます。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonAITalk(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
