import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/aitalk5_menu.png';
import SaveTabImage from './images/aitalk5_save.png';
import VoiceTabImage from './images/aitalk5_voice.png';
import StepperDialogButton from './StepperDialogButton';
import InputImage from './images/aitalk5_input.png';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '「オプション」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="270" height="102" />
      <Typography>
        エディターのメインメニューから［ツール(T)］→［オプション(O)...］を選びます。
      </Typography>
    </>,
  ],
  [
    '「ボイス」タブを設定する',
    <>
      <img src={VoiceTabImage} alt="設定例" width="469" height="270" />
      <Typography>
        「ボイス」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「入力テキスト中でボイスプリセットタグを有効にする」にチェックをつける</li>
        <li>「プロンプト文字列」を「＞」に設定する</li>
      </ol>
    </>,
  ],
  [
    '「音声保存」タブを設定する',
    <>
      <img src={SaveTabImage} alt="設定例" width="469" height="344" />
      <Typography>
        「音声保存」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「テキストファイルを音声ファイルと一緒に保存する」にチェックをつける</li>
      </ol>
    </>,
  ],
  [
    '「オプション」ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、「オプション」ダイアログを閉じます。
      </Typography>
    </>,
  ],
  [
    '参考: セリフの入力方法について',
    <>
      <img src={InputImage} alt="入力例" width="392" height="195" />
      <Typography>
        セリフを入力する際は、必ずボイスプリセット名を付けた状態でセリフを入力します。<br />
        左側のキャラクター一覧から文章入力欄へキャラクターをドラッグすることで入力できます。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonAITalk5(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
