import React from 'react';
import { IconButton, Tooltip } from '@material-ui/core';
import { Close as CloseIcon, ArrowDownward, ArrowUpward } from '@material-ui/icons';

export type TActions = { type: 'move-up' | 'move-down' | 'delete' };

export const ActionButtons = React.memo(function ActionButtons({
  name,
  index,
  length,
  disabled,
  dispatch,
}: {
  name: string;
  index: number;
  length: number;
  disabled?: boolean;
  dispatch: React.Dispatch<TActions>;
}) {
  const handleMoveUp = () => dispatch({ type: 'move-up' });
  const handleMoveDown = () => dispatch({ type: 'move-down' });
  const handleDelete = () => dispatch({ type: 'delete' });
  const canUp = !disabled && length >= 2 && index > 0;
  const canDown = !disabled && length >= 2 && index < length - 1;
  const moveUpLabel = canUp ? `${name}を上に移動` : '';
  const moveDownLabel = canDown ? `${name}を下に移動` : '';
  const deleteLabel = `${name}を削除`;
  return (
    <>
      <Tooltip title={moveUpLabel}>
        <span>
          <IconButton aria-label={moveUpLabel} disabled={!canUp} onClick={handleMoveUp}>
            <ArrowUpward />
          </IconButton>
        </span>
      </Tooltip>
      <Tooltip title={moveDownLabel}>
        <span>
          <IconButton aria-label={moveDownLabel} disabled={!canDown} onClick={handleMoveDown}>
            <ArrowDownward />
          </IconButton>
        </span>
      </Tooltip>
      <Tooltip title={deleteLabel}>
        <IconButton aria-label={deleteLabel} disabled={disabled} onClick={handleDelete}>
          <CloseIcon />
        </IconButton>
      </Tooltip>
    </>
  );
});
