import { Typography } from '@material-ui/core';
import React from 'react';
import SaveImage from './images/voiceroidutil_save.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '設定を行う',
    <>
      <img src={SaveImage} alt="設定例" width="402" height="228" />
      <Typography>
        「設定(2)」タブを開き、その中の「音声保存(A)」タブの赤枠内を以下のように設定します。
      </Typography>
      <ol>
        <li>
          {
            tmpPath ?
              <>
                「保存先(D)...」ボタンを押し、<Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> に設定する
              </>
              :
              <>
                「保存先(D)...」ボタンを押し、forcepser.exe（かんしくんのメインプログラム）と同じフォルダー内にある「tmp」フォルダーを選択する
                <Typography variant="body2">
                  ※もし「tmp」フォルダーが見つからない場合は、手動で作成してください。
                </Typography>
              </>
          }
        </li>
        <li>ファイル命名を「日時_キャラ名_入力文」に設定する</li>
        <li>「テキストファイルを必ず作成(T)」にチェックをつける</li>
        <li>「文字コード UTF-8 で作成(U)」のチェックを外す</li>
        <li>「AviUtl拡張編集ファイル(.exo)を作成(E)」のチェックを外す</li>
      </ol>
    </>,
  ],
];

export default function StepperSetUpButtonVoiceroidUtil(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
